export default {
	data() {
		return {};
	},
	props: {
		currentValue:{
			type:[String,Number],
			default:'0'
		},
		maxValue:{
			type:[String,Number],
			default:'0'
		},
	},
	methods: {
		getPercent(num, total) {
			if(total == '*') return '100%';
			num = parseFloat(num);
			total = parseFloat(total);
			if (isNaN(num) || isNaN(total)) {
				return '-';
			}
			return total <= 0 ? '0%' : Math.round((num / total) * 10000) / 100.0 + '%';
		}
	}
};
